import React from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import Layout from "../components/layout";
import HeroOther from "../components/heroOthers";

import SearchEngineOptimization from "../components/seo";
import ClientsCard from '../components/cards/for-our-clients';

const ForOurClientsPage = () => {
  let data = useStaticQuery(graphql`query ForOurClientsPage {
  pagesYaml(page: {eq: "forOurClients"}) {
    intro
    title
    steps {
      title
      desc
      linkText
      link
    }
    bottomText
    bottomTextBold
  }
  PricingIllustrationImage: file(
    relativePath: {eq: "illustration/for-our-clients-illustration.png"}
  ) {
    childImageSharp {
      gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED, backgroundColor: "white")
    }
  }
}
`);
  const data_yaml = data.pagesYaml;
  return (
    <div className="pricing">
      <SearchEngineOptimization
        pageTitle={data.pagesYaml.title}
        description="Affordable Cannabis CPA for dispensaries, retailers and growers in the state of California. I offer different pricing packages and we are always at front about our fees. Check our website for more information."
      />
      <Layout>
        <HeroOther
          title="for my clients"
          subtitle={["Find the plan thatâ€™s just right for your business"]}
          image={data.PricingIllustrationImage.childImageSharp.gatsbyImageData}
          intro={data_yaml.intro}
        />
        <div className="pricing__cardContainer section container">
          {data_yaml.steps.map((item, index) => (
            <ClientsCard
              key={index}
              title={item.title}
              description={item.desc}
              linkText={item.linkText}
              link={item.link}
            />
          ))}
        </div>

        <div className="descrtion container">
          <p>
            <strong>{data_yaml.bottomTextBold}</strong> {data_yaml.bottomText}
          </p>
        </div>
      </Layout>
    </div>
  );
};

export default ForOurClientsPage;
